import React, { useState } from 'react';
import styles from './Testimonial.module.css';
import gsap from 'gsap';
import vlad from '../../img/vlad.png';
// Import celelalte imagini similare
import marius from '../../img/vlad.png'; // nlocuiete cu calea real
import daniel from '../../img/vlad.png'; // nlocuiete cu calea real
import maxim from '../../img/vlad.png'; // nlocuiete cu calea real
import alina from '../../img/vlad.png'; // nlocuiete cu calea real

// Array de recenzii pentru membrii STUDEM
const people = [
  {
    photo: vlad,
    name: "Vlad Goncear",
    profession: "MEMBRU BOARD",
    description: "Schimbarea se afl n noi, cei care vor s modeleze viitorul acionnd n prezent, anume din aceste considerente eu am luptat de a fi n STUDEM i n al treilea mandat. Cltoria mea ca Voluntar al Centrului de Tineret a luat sfrit, prin realizarea a ceva mai bun pentru Sectorul de Tineret din municipiul Bli, iar acum ofer ansa i altor Tineri de a schimba viitorul prin aciunile din prezent."
  },
  {
    photo: marius,
    name: "Marius Toderic",
    profession: "MEMBRU BOARD",
    description: "Nu e chiar att de greu s faci primul pas!"
  },
  {
    photo: daniel,
    name: "Daniel Bulgaru",
    profession: "MEMBRU BOARD",
    description: "STUDEM e ca o gur de aer proaspt, nvei s trieti viaa din nou!"
  },
  {
    photo: maxim,
    name: "Maxim Clrai",
    profession: "MEMBRU BOARD",
    description: "STUDEM a fost un loc n care am descoperit nu doar cunotine noi, ci i o comunitate plin de energie i entuziasm. Datorit Centrului, am ctigat ncredere n propria mea capacitate de a nva i de a crete profesional i personal."
  },
  {
    photo: alina,
    name: "Alina Bogaci",
    profession: "MEMBRU BOARD",
    description: "E imposibil s redau n doua promoii toate cunotinele, emoiile i experienele ce le-am acumulat n doi ani i jumtate fiind membr a familie STUDEM. STUDEM a fost startul meu n via, m-a fcut s-mi gsesc punctele tari a caracterului i mi-a perfectat neajunsurile. Formarea mea ca personalitate a avut loc aici, la STUDEM. Mereu n inim i n gnd. Spun succes celor ce se altur familiei. "
  }
];

const App = () => {
  const [currentPerson, setCurrentPerson] = useState(0);

  // Functia pentru schimbarea recenziei
  const slide = (whichSide) => {
    let sideSymbol = whichSide === 'left' ? '' : '-';
    let sideSymbolOpp = whichSide === 'left' ? '-' : '';

    gsap.timeline()
      .to(`.${styles.reviewWrap}`, {
        duration: 0.4,
        opacity: 0,
        x: `${sideSymbol}${window.innerWidth}px`
      })
      .to(`.${styles.reviewWrap}`, {
        duration: 0,
        x: `${sideSymbolOpp}${window.innerWidth}px`
      })
      .call(() => {
        setCurrentPerson(prevPerson => (prevPerson + (whichSide === 'left' ? 1 : -1) + people.length) % people.length);
      })
      .to(`.${styles.reviewWrap}`, {
        duration: 0.4,
        opacity: 1,
        x: 0
      });
  };

  return (
    <div className={styles.contentWrapper}>
      <h1>Ce spune Familia STUDEM</h1>
      <div className={styles.blueLine}></div>
      <div className={styles.wrapperForArrows}>
        <div className={styles.reviewWrap}>
          <div
            className={styles.imgDiv}
            style={{ backgroundImage: `url(${people[currentPerson].photo})` }}
          ></div>
          <div className={styles.personName}>{people[currentPerson].name}</div>
          <div className={styles.profession}>{people[currentPerson].profession}</div>
          <div className={styles.description}>{people[currentPerson].description}</div>
        </div>
        <div className={`${styles.arrowWrap} ${styles.leftArrowWrap}`} onClick={() => slide('left')}>
          <div className={styles.arrow}></div>
        </div>
        <div className={`${styles.arrowWrap} ${styles.rightArrowWrap}`} onClick={() => slide('right')}>
          <div className={styles.arrow}></div>
        </div>
      </div>
    </div>
  );
};

export default App;
